<template>
  <router-view></router-view>
</template>

<script>
export default {
  name: 'app',
  created() {
    this.$store.dispatch('loadSettings')
    this.axios.get('dashboard/currentLocale')
    .then(({ data }) => {
      this.$store.dispatch('setLocale', data.locale)
      this.$i18n.locale = data.locale
    })
  }
}
</script>

