<template>
  <div class="menu-container">
    <div class="menu-trigger" @click="toggleMenu">
      <svg viewBox="0 0 32 32" width="32" height="32"><g><path d="M32,18.133V13.867l-4.593-.574a11.652,11.652,0,0,0-1.428-3.442L28.822,6.2,25.805,3.178,22.149,6.021a11.652,11.652,0,0,0-3.442-1.428L18.133,0H13.867l-.574,4.593A11.652,11.652,0,0,0,9.851,6.021L6.2,3.178,3.178,6.2,6.021,9.851a11.652,11.652,0,0,0-1.428,3.442L0,13.867v4.266l4.593.574a11.652,11.652,0,0,0,1.428,3.442L3.178,25.805,6.2,28.822l3.656-2.843a11.652,11.652,0,0,0,3.442,1.428L13.867,32h4.266l.574-4.593a11.652,11.652,0,0,0,3.442-1.428l3.656,2.843,3.017-3.017-2.843-3.656a11.652,11.652,0,0,0,1.428-3.442ZM16,21a5,5,0,1,1,5-5A5,5,0,0,1,16,21Z"></path></g></svg>
    </div>
    <nav class="dropdown-menu" :class="{'show': showMenu}">
      <span class="dropdown__label">{{$t('global.currency')}}</span>
      <ul class="dropdown__currency-list">
        <li v-for="(menuItem, index) in currencyMenu" :key="index" @click="selectDefault($event, menuItem.value)" :class="{selected: menuItem.selected}">
          <check-icon v-if="menuItem.selected"></check-icon>
          <span>{{menuItem.displayText}}</span>
        </li>
      </ul>
      <span class="dropdown__label">{{$t('global.shownFeed')}}</span>
      <ul>
        <li v-for="(menuItem, key, index) in feedTypes" v-if="menuItem.mutable" :key="index" @click="toggleFeed($event, key)" :class="{muted: menuItem.muted}">
          <hide-icon v-if="menuItem.muted" />
          <span>{{$t(`feed.types.${menuItem.type}`)}}</span>
        </li>
      </ul>
    </nav>
  </div>
</template>

<script>
import HideIcon from '../icons/not-visible.vue'
import CheckIcon from '../icons/check.vue'
import { mapGetters } from 'vuex'
export default {
  name: 'settings-menu',
  computed: {
    ...mapGetters(['feedTypes', 'currencyMenu', 'showMenu'])
  },
  methods: {
    selectDefault (event, currencyId) {
      event.stopPropagation()
      this.$store.dispatch('selectDefaultCurrency', currencyId)
      this.$emit('currencyChanged')
    },
    toggleFeed (event, type) {
      event.stopPropagation()
      this.$store.dispatch('toggleFeedType', type)
    },
    toggleMenu () {
      this.$store.dispatch('showMenu', !this.showMenu)
      if (this.showMenu) {
        window.setTimeout(() => {
          window.addEventListener('click', this.closeMenuHandler)
        }, 10)
      }
    },
    closeMenuHandler (event) {
      event.stopPropagation()
      this.$store.dispatch('showMenu', false)
      window.removeEventListener('click', this.closeMenuHandler)
    }
  },
  components: {
    HideIcon,
    CheckIcon
  }
}
</script>

